/* Navbar.css */

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  background-color: #854624; /* Change the background color as needed */
  color: white; /* Change the text color as needed */
}

.logo {
  display: flex;
  align-items: center;
}

.logo img {
  width: 100px; /* Adjust the width of the logo as needed */
  height: auto; /* Maintain the aspect ratio of the logo */
  margin-right: 10px; /* Add some spacing between the logo and text */
}

.nav-links {
  display: flex;
  align-items: center;
}

.nav-links a {
  margin-right: 200px; /* Adjust the spacing between navigation links as needed */
  text-decoration: none;
  color: white; /* Change the text color of navigation links as needed */
  font-size: 1.2rem; /* Increase font size */
  font-weight: bold; /* Add bold styling */
}

.nav-links a:hover {
  text-decoration: underline; /* Add underline on hover */
  font-size: 1.3rem; /* Increase font size on hover */
}

/* Add this new rule */
.nav-links {
  margin-left: auto; /* Push the nav-links to the right */
}
